.ciq-asset-information{position:relative;z-index:+1;background:transparent;display:flex;margin:15px 0px;left:10px;top:10px;justify-content:flex-start;opacity:1;height:14px;transition:all 0.1s}.ciq-asset-information.hide{height:0;opacity:0;margin:0;pointer-events:none}.ciq-asset-information>div{padding-right:5px;display:flex;justify-content:flex-start;align-items:center;font-size:12px}.ciq-asset-information>div div:first-child{padding:0 8px}.ciq-asset-information>div div:last-child{font-weight:bold}.smartcharts-light .ciq-asset-information>div{color:#333}.smartcharts-dark .ciq-asset-information>div{color:#aaacba}.smartcharts-mobile .ciq-asset-information{left:2px;flex-wrap:wrap}.smartcharts-mobile .ciq-asset-information>div{font-size:12px}.smartcharts-mobile .ciq-asset-information>div div:first-child{padding:0px 5px}
