@import url("https://fonts.googleapis.com/css?family=Roboto:400,700,500,100,300,400italic|Roboto+Condensed:400,700,300");
html,body{-ms-touch-action:none;margin:0px}
.chartContainer{display:block;font-family:"Roboto",helvetica,sans-serif;font-size:12px;position:relative;z-index:1;transition:height 0.4s ease-in-out}
.smartcharts-light .chartContainer{background:#fff;color:#aaacba}
.smartcharts-dark .chartContainer{background:#2a3052;color:#aaacba}
.chartContainer.ciq-draw-mode{cursor:none}
.ciq-chart{height:auto;width:100%}
.smartcharts-light .ciq-chart{background:#fff}
.smartcharts-dark .ciq-chart{background:#2a3052}
.cq-chart-control-left .stx-panel-control{left:10px}
.cq-chart-control-left .chartContainer{width:calc(100% - 90px);margin-left:90px}
.sharing .ciq-no-share{display:none !important;left:-9999px !important}
.stx-btn{display:inline-block;cursor:pointer;padding:0px 8px;border:solid 1px #ddd;font-weight:bold;line-height:27px;color:#7e7e7e;text-align:center;overflow:visible;background-repeat:no-repeat;border-radius:3px;transition:color 0.25s, border 0.25s, box-shadow 0.25s;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}
.stx-btn:hover{color:#444444;border:solid 1px #c5c5c5;box-shadow:0px 1px 1px rgba(0,0,0,0.1),inset 0px -8px 6px rgba(100,100,100,0.03)}
.stx-btn:active{color:#333;box-shadow:inset 0px 8px 6px rgba(100,100,100,0.03)}
.stx-btn-panel,.stx-btn-panel span{display:inline-block;width:25px;height:25px;cursor:pointer;transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s}
.stx-btn-panel{margin:3px 2px;opacity:0;transition:opacity 0.5s}
.smartcharts-light .stx-btn-panel{background-color:#fff}
.smartcharts-dark .stx-btn-panel{background-color:#2a3052}
.stx-btn-panel:hover span,.stx-btn-panel:active span{opacity:1}
.stx-btn-panel:active{-webkit-transform:scale(1.2, 1.2);transform:scale(1.2, 1.2)}
.stx-btn-panel>span{background:no-repeat center}
.stx-btn-panel>span.stx-ico-up{background-image:url("./icons/chart/ic-up.svg")}
.stx-btn-panel>span.stx-ico-down{background-image:url("./icons/chart/ic-down.svg")}
.stx-btn-panel>span.stx-ico-close{background-image:url("./icons/delete/ic-delete.svg")}
.stx-btn-panel>span.stx-ico-edit{background-image:url("./icons/chart/ic-settings.svg")}
.stx-btn-panel>span.stx-ico-focus{background-image:url("./icons/chart/ic-maximize.svg")}
.smartcharts-dark .stx-btn-panel>span.stx-ico-up{background-image:url("./icons/chart/ic-up-dark.svg")}
.smartcharts-dark .stx-btn-panel>span.stx-ico-down{background-image:url("./icons/chart/ic-down-dark.svg")}
.smartcharts-dark .stx-btn-panel>span.stx-ico-close{background-image:url("./icons/delete/ic-delete-dark.svg")}
.smartcharts-dark .stx-btn-panel>span.stx-ico-edit{background-image:url("./icons/chart/ic-settings-dark.svg")}
.smartcharts-dark .stx-btn-panel>span.stx-ico-focus{background-image:url("./icons/chart/ic-maximize-dark.svg")}
.stx_chart_controls{position:absolute;display:block;bottom:60px;left:50%;margin-left:-55px;height:27px;margin-bottom:15px;width:auto;text-align:center;z-index:30}
.stx_jump_today:hover,.stx_jump_today:active{opacity:1}
.stx_jump_today{color:#fff;overflow:hidden;position:absolute;bottom:12px;right:10px;line-height:0px;height:25px;width:30px;padding:0px;margin-bottom:17px;border-radius:3px;background:#fafafa;box-shadow:0px 1px 1px rgba(0,0,0,0.2);transition:background 0.25s;cursor:pointer;z-index:30}
.stx_jump_today span{display:inline-block;line-height:25px;height:25px;width:30px;padding-top:25px;background:url("./icons/chart/jump-to-today.svg") no-repeat center;transition:opacity 0.25s, -webkit-transform 0.2s;transition:opacity 0.25s, transform 0.2s;transition:opacity 0.25s, transform 0.2s, -webkit-transform 0.2s;opacity:0.6}
.stx_jump_today:hover span{opacity:1}
.stx-baseline-handle{background:url("./icons/chart/resize-icon.svg") no-repeat center;cursor:ns-resize;display:none;height:20px;opacity:0.5;position:absolute;width:20px;z-index:30}
.stx-baseline-handle:hover{opacity:1}
.stx-drag-chart{cursor:url("./icons/chart/closedhand.cur"),default !important}
.stx-panel-control{position:absolute;top:4px;left:0px;overflow:hidden;z-index:30;display:none;margin:5px;border-radius:3px}
.smartcharts-light .stx-panel-control{background:#fff;border:1px solid #e9e9ed}
.smartcharts-dark .stx-panel-control{background:#2a3052;border:1px solid rgba(170,172,186,0.3)}
.stx-panel-control .stx-btn-panel{opacity:1}
.stx-panel-control *{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}
.stx-panel-control.stx-show{display:inline-block}
.stx-panel-control .stx-btn{float:left}
.stx-panel-control .stx-panel-title{box-shadow:none;margin:5px 10px;float:left;display:inline-block;color:#333;padding:0 10px;line-height:22px;font-size:12px;text-align:left;text-transform:uppercase}
.smartcharts-light .stx-panel-control .stx-panel-title{border-right:1px solid #e9e9ed}
.smartcharts-dark .stx-panel-control .stx-panel-title{border-right:1px solid rgba(170,172,186,0.3)}
.smartcharts-light .stx-panel-control .stx-panel-title{color:#333}
.smartcharts-dark .stx-panel-control .stx-panel-title{color:#fff}
#mSticky{position:absolute;display:none;z-index:40;font-size:12px;text-align:center;white-space:nowrap;border-radius:3px}
.smartcharts-light #mSticky{color:#fff;background:rgba(51,51,51,0.88)}
.smartcharts-dark #mSticky{color:#2a3052;background:rgba(255,255,255,0.8)}
#mSticky #mStickyInterior{padding:8px 8px 4px;position:relative;width:100%;border-radius:3px 3px 0px 0px;font-weight:500;border:none;box-sizing:border-box}
.smartcharts-light #mSticky #mStickyInterior{background:#333}
.smartcharts-dark #mSticky #mStickyInterior{background:#fff}
#mSticky #mStickyRightClick{position:relative}
#showMeasure{display:none;font-size:11px;white-space:nowrap;z-index:40;border-radius:3px}
.smartcharts-light #showMeasure{color:#fff;background:rgba(51,51,51,0.88);box-shadow:rgba(21,33,45,0.32)}
.smartcharts-dark #showMeasure{color:#2a3052;background:rgba(255,255,255,0.8);box-shadow:rgba(21,33,45,0.32)}
.smartcharts-light #showMeasure:before{border-color:rgba(51,51,51,0.88) transparent}
.smartcharts-dark #showMeasure:before{border-color:rgba(255,255,255,0.8) transparent}
#vectorTrashCan,#showMeasure,.overlayEdit{position:absolute;z-index:40}
#sMeasure{margin:0px;padding:5px 10px;display:inline-block;float:left}
.overlayEdit.stx-btn,#vectorTrashCan.stx-btn{cursor:pointer;display:inline-block;text-align:center;width:30px;height:30px;padding:0;border:none;border-radius:3px}
.smartcharts-light .overlayEdit.stx-btn,.smartcharts-light #vectorTrashCan.stx-btn{background:rgba(51,51,51,0.88) !important}
.smartcharts-dark .overlayEdit.stx-btn,.smartcharts-dark #vectorTrashCan.stx-btn{background:rgba(255,255,255,0.8) !important}
.overlayEdit.stx-btn span,#vectorTrashCan.stx-btn span{display:block;height:30px;width:30px !important;overflow:hidden;text-indent:100px}
.smartcharts-light .overlayEdit.stx-btn span,.smartcharts-light #vectorTrashCan.stx-btn span{background:url("./icons/chart/ic-settings-dark.svg") no-repeat center !important}
.smartcharts-dark .overlayEdit.stx-btn span,.smartcharts-dark #vectorTrashCan.stx-btn span{background:url("./icons/chart/ic-settings-dark.svg") no-repeat center !important}
.rightclick_study .overlayEdit{margin:0px !important}
.smartcharts-light .rightclick_series .overlayEdit.stx-btn span,.smartcharts-light .rightclick_series #vectorTrashCan.stx-btn span{background:url("./icons/delete/ic-delete-dark.svg") no-repeat center !important}
.smartcharts-dark .rightclick_series .overlayEdit.stx-btn span,.smartcharts-dark .rightclick_series #vectorTrashCan.stx-btn span{background:url("./icons/delete/ic-delete-dark.svg") no-repeat center !important}
#overlayTrashCan{display:none !important}
#mouseDeleteInstructions{margin:8px;background:transparent;display:block}
#mouseDeleteText,#mouseManageText{display:none}
.rightclick_drawing #mouseManageText{display:inline}
.rightclick_series #mouseDeleteText{display:inline}
.stx-loader{position:absolute;top:280px;left:50%;margin-left:-20px;width:40px;height:40px;z-index:50}
.smartcharts-light .stx_current_hr_up,.smartcharts-light .stx_current_hr_down{color:#fff;background-color:#2a3052}
.smartcharts-dark .stx_current_hr_up,.smartcharts-dark .stx_current_hr_down{color:#2a3052;background-color:#fff}
.stx-float-price,.stx-float-date{border-radius:3px}
.smartcharts-light .stx-float-price,.smartcharts-light .stx-float-date{background-color:#4a4f6c}
.smartcharts-dark .stx-float-price,.smartcharts-dark .stx-float-date{background-color:#4a4f6c}
.stx-float-date{position:absolute;font-size:10px;font-family:'Helvetica';z-index:30;color:#fff;padding:1px 6px;text-align:center;width:70px}
.stx-float-price{color:#fff;background-color:#333}
.stx_crosshair{position:absolute;z-index:10}
.smartcharts-light .stx_crosshair{background-color:#4a4f6c}
.smartcharts-dark .stx_crosshair{background-color:#fff}
.stx_crosshair_drawing{position:absolute;z-index:10}
.smartcharts-light .stx_crosshair_drawing{background-color:#4a4f6c}
.smartcharts-dark .stx_crosshair_drawing{background-color:#fff}
#crossY,.stx_crosshair_y{left:0px;height:1px;width:98%;margin-top:0}
#crossX,.stx_crosshair_x{top:0px;width:1px;height:100%}
.stx-crosshair-on{cursor:crosshair}
.stx_panels{font-family:'Helvetica';font-size:10px;color:#fff}
.stx_panel_background{color:#7c878b}
.stx_panel_border{width:1px;color:#cccccc}
.stx_solo_lit span{background-color:#b1b7b8;color:#ffffff;opacity:1}
.stx_grid{width:1px;opacity:1;border-style:solid}
.smartcharts-light .stx_grid{background:#fff;color:#f4f4f6}
.smartcharts-dark .stx_grid{background:#2a3052;color:rgba(170,172,186,0.08)}
.stx_grid_border{color:#999999;border-style:solid;opacity:0}
.stx_line_chart{width:1px;border-top-style:dotted}
.stx_bar_chart,.stx_bar_up,.stx_bar_down,.stx_bar_even{width:1px}
.smartcharts-light .stx_line_chart,.smartcharts-light .stx_bar_chart{color:#2a3052}
.smartcharts-dark .stx_line_chart,.smartcharts-dark .stx_bar_chart{color:#aaacba}
.stx_bar_down{color:#f95454}
.stx_bar_up{color:#4caf50}
.stx_baseline_delta_mountain{background-color:rgba(163,102,255,0.5);color:rgba(163,102,255,0.01);padding:3px}
.stx_colored_mountain_chart{background-color:rgba(163,102,255,0.5);color:rgba(163,102,255,0.01);width:2px;padding:4px}
.stx_mountain_chart{color:rgba(0,156,255,0.01);background-color:rgba(0,102,255,0.5);border:#009cff;width:2px}
.stx_histogram_down{color:#f95454}
.smartcharts-light .stx_histogram_down{border-left-color:#e9e9ed}
.smartcharts-dark .stx_histogram_down{border-left-color:rgba(170,172,186,0.3)}
.stx_histogram_up{color:#4caf50}
.smartcharts-light .stx_histogram_up{border-left-color:#e9e9ed}
.smartcharts-dark .stx_histogram_up{border-left-color:rgba(170,172,186,0.3)}
.stx_histogram_even{color:#999999}
.smartcharts-light .stx_histogram_even{border-left-color:#e9e9ed}
.smartcharts-dark .stx_histogram_even{border-left-color:rgba(170,172,186,0.3)}
.stx_highlight_vector{color:#ff0000;width:3px}
.stx_candle_shadow,.stx_bar_even{color:#2e383b}
.stx_candle_down,.stx_line_down{color:#f95454;border-left-color:transparent}
.stx_candle_up,.stx_line_up{color:#4caf50;border-left-color:transparent}
.stx_hollow_candle_down{color:#f95454}
.stx_hollow_candle_up{color:#4caf50}
.stx_hollow_candle_even{color:#888888}
.stx_baseline{color:#2e383b}
.stx_baseline_down{color:#f95454;width:2px}
.stx_baseline_up{color:#4caf50;width:2px}
.stx_kagi_down{color:#f95454;width:1px}
.stx_kagi_up{color:#4caf50;width:3px}
.stx_pandf_down{color:#f95454;padding:2px 0px;width:2px}
.stx_pandf_up{color:#4caf50;padding:2px 0px;width:2px}
.stx_volume_up{color:#4caf50}
.smartcharts-light .stx_volume_up{border:#e9e9ed}
.smartcharts-dark .stx_volume_up{border:rgba(170,172,186,0.3)}
.stx_volume_down{color:#f95454}
.smartcharts-light .stx_volume_down{border:#e9e9ed}
.smartcharts-dark .stx_volume_down{border:rgba(170,172,186,0.3)}
.stx_volume_underlay_up{color:#8cc176;opacity:0.3;border-left-color:#000}
.stx_volume_underlay_down{color:#b82d0c;opacity:0.3;border-left-color:#000}
.stx_volume_profile{opacity:0.3;border-color:#000}
.stx_projection_new{color:#0000ff}
.stx_gaps{color:#b1b7b8;opacity:0.3}
.stx_histogram{opacity:0.2}
.smartcharts-light .stx_histogram{color:#aaacba}
.smartcharts-dark .stx_histogram{color:#aaacba}
.stx_annotation{font-size:13px;font-family:'Helvetica';background:transparent;box-shadow:none;padding:0px 6px;outline:none;color:#333;resize:none;z-index:1000}
.smartcharts-light .stx_annotation{border:1px solid #e9e9ed}
.smartcharts-dark .stx_annotation{border:1px solid rgba(170,172,186,0.3)}
.stx_annotation_bg{color:#f5f5f5}
.stx_annotation_highlight{color:#fff}
.stx_annotation_highlight_bg{color:#3d4447}
#annotationSave,#annotationCancel,.stx-btn.stx_annotation_save,.stx-btn.stx_annotation_cancel{position:absolute;display:none;font-size:11px;box-shadow:0px 1px 2px rgba(0,0,0,0.2);z-index:50}
#annotationCancel,.stx_annotation_cancel{margin-left:10px}
.stx_watermark{font-size:16px;font-family:'Helvetica'}
.stx_fib_levels{font-size:12px;font-family:'Helvetica';color:transparent}
.stx_share{font-size:14px;font-family:'georgia';width:1px}
.smartcharts-light .stx_share{color:#aaacba}
.smartcharts-dark .stx_share{color:#aaacba}
.stx_share_symbol{font-size:14px;font-family:'Arial';font-weight:bold}
.stx-holder{display:block;position:absolute;overflow:hidden;z-index:20}
.stx-subholder{position:absolute;overflow:hidden}
.stx_market_session.divider{background-color:rgba(0,255,0,0.8);width:1px}
.stx_market_session.pre{background-color:rgba(255,255,0,0.1)}
.stx_market_session.post{background-color:rgba(0,0,255,0.2)}
.stx_grid_dark{opacity:1;opacity:0.9;border-style:solid}
.smartcharts-light .stx_grid_dark{color:#f4f4f6}
.smartcharts-dark .stx_grid_dark{color:rgba(170,172,186,0.08)}
.stx_yaxis,.stx_xaxis,.stx_xaxis_dark{font-size:10px;font-family:'Roboto'}
.smartcharts-light .stx_yaxis,.smartcharts-light .stx_xaxis,.smartcharts-light .stx_xaxis_dark{color:#aaacba}
.smartcharts-dark .stx_yaxis,.smartcharts-dark .stx_xaxis,.smartcharts-dark .stx_xaxis_dark{color:#aaacba}
.cq-down{color:#f95454}
.cq-up{color:#4caf50}
.stx-holder:hover .stx-btn-panel{opacity:1;transition:opacity 0.5s, height 0.3s}
.stx-ico-handle{opacity:1;position:absolute;left:0%;height:1px;width:100%;overflow:hidden;cursor:ns-resize;z-index:20}
.smartcharts-light .stx-ico-handle{box-shadow:0 0 0px 8px #fff;background:#fff}
.smartcharts-dark .stx-ico-handle{box-shadow:0 0 0px 8px #2a3052;background:#2a3052}
.stx-ico-handle span{padding:100%}
.smartcharts-light .stx-ico-handle span{background:#e9e9ed}
.smartcharts-dark .stx-ico-handle span{background:rgba(170,172,186,0.3)}
.stx-ico-handle:hover{opacity:0.3}
.stx-ico-handle:active{cursor:ns-resize}
.stx-ico-handle.stx-grab{opacity:0.3;height:2px}
.smartcharts-light .stx-ico-handle.stx-grab{background-color:#e9e9ed}
.smartcharts-dark .stx-ico-handle.stx-grab{background-color:rgba(170,172,186,0.3)}
.stx_panel_drag{color:transparent}
.stx_xaxis,.stx_yaxis,.stx_watermark{font-family:"Roboto",helvetica,sans-serif}
.sidenav.active{display:flex}
.sidenav{height:250px;width:40px;position:fixed;z-index:2;right:0;display:none;flex-direction:column;align-items:center;align-content:center;padding:0}
.smartcharts-light .sidenav{border-left:1px solid #e9e9ed}
.smartcharts-dark .sidenav{border-left:1px solid rgba(170,172,186,0.3)}
.ciq-menu>span.icons-on{margin-top:-3px}
.stx_range_slider.shading{background-color:rgba(128,128,128,0.3);border:solid 2px #0090b7;width:5px}
.stx-chart-panel.stx-show{display:none}
.ciq-chart.ciq-screenshot .cq-menu-dropdown{display:none !important}
.ps--active-x>.ps__rail-x,.ps--active-y>.ps__rail-y{background-color:transparent !important}
.cq-spot{width:6px;height:6px;padding:0px;line-height:1px;border-radius:100%;cursor:pointer;position:absolute;background:transparent;-webkit-animation:pulse 1.6s infinite;animation:pulse 1.6s infinite;margin-left:-3px;margin-top:-3px;box-shadow:0 0 0 rgba(2,152,211,0.6)}
.cq-spot:before{content:'\25CF';font-size:18px;color:#0298d3;position:relative;left:-2.3px;line-height:5px}
