@keyframes pulse{0%{box-shadow:0 0 0 0 rgba(2,152,211,0.6)}70%{box-shadow:0 0 0 10px rgba(2,152,211,0)}100%{box-shadow:0 0 0 0 rgba(2,152,211,0)}}
.smartcharts-mobile #mSticky{background:none}
.smartcharts-mobile #mSticky #mStickyInterior{margin:0px;padding:0px 8px 0 !important;line-height:30px;border-radius:3px 0px 0px 3px;width:auto;z-index:99}
.smartcharts-mobile #mSticky #mStickyRightClick{margin-left:-2px !important}
.stx-textarea{padding:0 5px 0 10px;box-sizing:border-box;-webkit-user-select:text;-moz-user-select:text;-ms-user-select:text;user-select:text}
.smartcharts-light .stx-textarea{border-left:1px solid rgba(170,172,186,0.3)}
.smartcharts-dark .stx-textarea{border-left:1px solid rgba(170,172,186,0.3)}
