.smartcharts-light #annotationCancel.ciq-primary,.smartcharts-light #annotationCancel.ciq-primary,.smartcharts-light #annotationSave.ciq-primary,.smartcharts-light #annotationSave.ciq-primary{background:rgba(170,172,186,0.3)}
.smartcharts-dark #annotationCancel.ciq-primary,.smartcharts-dark #annotationCancel.ciq-primary,.smartcharts-dark #annotationSave.ciq-primary,.smartcharts-dark #annotationSave.ciq-primary{background:rgba(170,172,186,0.3)}
#annotationCancel.ciq-primary:hover,#annotationCancel.ciq-primary:active,#annotationCancel.ciq-primary:hover,#annotationCancel.ciq-primary:active,#annotationSave.ciq-primary:hover,#annotationSave.ciq-primary:active,#annotationSave.ciq-primary:hover,#annotationSave.ciq-primary:active{color:#fff}
.smartcharts-light #annotationCancel.ciq-primary:hover,.smartcharts-light #annotationCancel.ciq-primary:active,.smartcharts-light #annotationCancel.ciq-primary:hover,.smartcharts-light #annotationCancel.ciq-primary:active,.smartcharts-light #annotationSave.ciq-primary:hover,.smartcharts-light #annotationSave.ciq-primary:active,.smartcharts-light #annotationSave.ciq-primary:hover,.smartcharts-light #annotationSave.ciq-primary:active{background:rgba(170,172,186,0.3)}
.smartcharts-dark #annotationCancel.ciq-primary:hover,.smartcharts-dark #annotationCancel.ciq-primary:active,.smartcharts-dark #annotationCancel.ciq-primary:hover,.smartcharts-dark #annotationCancel.ciq-primary:active,.smartcharts-dark #annotationSave.ciq-primary:hover,.smartcharts-dark #annotationSave.ciq-primary:active,.smartcharts-dark #annotationSave.ciq-primary:hover,.smartcharts-dark #annotationSave.ciq-primary:active{background:rgba(170,172,186,0.3)}
