#annotationCancel.ciq-primary.ciq-simple:hover,#annotationCancel.ciq-primary.ciq-simple:active,#annotationCancel.ciq-primary.ciq-simple:hover,#annotationCancel.ciq-primary.ciq-simple:active,#annotationSave.ciq-primary.ciq-simple:hover,#annotationSave.ciq-primary.ciq-simple:active,#annotationSave.ciq-primary.ciq-simple:hover,#annotationSave.ciq-primary.ciq-simple:active{box-shadow:0,0,0,0,rgba(0,0,0,0)}
