.ciq-list{max-height:260px;overflow-y:auto;position:relative}.ciq-list.ps{padding-right:11px}.ciq-list.scrollbar-container .ciq-list-item:first-child{margin-top:16px}.ciq-list.scrollbar-container .ciq-list-item:nth-last-child(-n+3){margin-bottom:16px}.ciq-list .ciq-list-item{cursor:pointer;line-height:32px;font-size:12px;height:32px;padding:0 0 0 16px;display:flex;align-items:center;justify-content:flex-start;border-bottom:none;box-sizing:border-box}.smartcharts-light .ciq-list .ciq-list-item{color:#333;background:#fff;border-left:1px solid #e9e9ed;border-right:1px solid #e9e9ed;border-bottom:1px solid #e9e9ed}.smartcharts-dark .ciq-list .ciq-list-item{color:#aaacba;background:#2a3052;border-left:1px solid rgba(170,172,186,0.3);border-right:1px solid rgba(170,172,186,0.3);border-bottom:1px solid rgba(170,172,186,0.3)}.smartcharts-light .ciq-list .ciq-list-item:first-child{border-top:1px solid #e9e9ed}.smartcharts-dark .ciq-list .ciq-list-item:first-child{border-top:1px solid rgba(170,172,186,0.3)}.ciq-list .ciq-list-item.active{font-weight:bold}.smartcharts-light .ciq-list .ciq-list-item.active{color:#2a3052;background-color:#f4f4f6}.smartcharts-dark .ciq-list .ciq-list-item.active{color:#fff;background-color:#3f4463}.ciq-list .ciq-list-item.disabled{pointer-events:none}.ciq-list .ciq-list-item.disabled>*{opacity:0.2;cursor:auto}
